<template>
  <div>
    <div class="content_title" v-if='datalist.length<10||showMore'>日志列表</div>
    <div class="content_title" v-else>最近10篇日志</div>
    <el-divider></el-divider>
    <template v-if="showMore">
      <HomeUrl
        v-for="(item,index) in datalist"
        :key="index"
        :href="item.href"
        :text="item.text"
        :target="item.target"
      >
        <span style="float:right;font-size:0.7em;">{{item.date}}</span>
      </HomeUrl>
    </template>
    <template v-else>
      <HomeUrl
        v-for="(item,index) in datalist.slice(0,10)"
        :key="index"
        :href="item.href"
        :text="item.text"
        :target="item.target"
      >
        <span style="float:right;font-size:0.7em;">{{item.date}}</span>
      </HomeUrl>
      <div id="more" v-if='datalist.length>10' @click="show">更多日志...</div>
    </template>
  </div>
</template>
<script>
import HomeUrl from "@/components/HomeUrl.vue";
export default {
  data: function() {
    return {
      datalist: [],
      showMore: false
    };
  },
  components: { HomeUrl },
  created() {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData();
  },
  methods: {
    fetchData() {
      // replace getPost with your data fetching util / API wrapper
      var geturl = "/blogjson/bloglist.json";
      this.$http
        .get(geturl)
        .then(response => {
          this.datalist = response.data.datalist;
        })
        .catch(function(error) {
          // eslint-disable-next-line
          console.log(error);
        });
    },
    show() {
      this.showMore = true;
    }
  }
};
</script>
<style lang="less" scoped>
#more {
  font-size: 1.6em;
  cursor: pointer;
  &:hover{
      color: #409EFF;
  }
}
</style>